import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { TaskServiceProvider } from '../../providers/task-service/task-service';
import { InputDialogServiceProvider } from '../../providers/input-dialog-service/input-dialog-service';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  
  constructor(public navCtrl: NavController, public alertCtrl: AlertController, public dataService: TaskServiceProvider, public inputService: InputDialogServiceProvider) {

  }

  loadTasks() {
    return this.dataService.getTasks();
  }
  deleteTask(task, index) {
    console.log("Removing task", task, index);

    this.dataService.removeTask(index);
  }

  editTask(task, index) {
    console.log("Editing task", task, index);

    this.inputService.openModal(task, index);
  }

  addTask() {
    console.log("Adding item");
    this.inputService.openModal();
  }

}



